import React, {Component} from 'react'

class Home extends Component{
    render(){
        return(
            <div className="home-container">
                <h3>“摄影的重要价值之一，就是我们可以与最爱的人分享，不论是父母、朋友、邻居都能够参与当中，甚至我们可以透过摄影把爱传给下一代。”</h3>
            </div>
        )
    }
}

export default Home